import Vue from 'vue'
import Vuex from 'vuex'
import getters from './getters'
import actions from './action'
import mutations from './mutations'

Vue.use(Vuex)

const state = {
	latitude: '', // 
	longitude: '', // 
	cartList: {}, // 
	shopDetail: null, //
	userInfo: null, //
	shopid: null,//id
	remarkText: null,//
	inputText: '',//
	invoice: false,//
	newAddress: [], //
	searchAddress: null,//
	geohash: '31.22299,121.36025',//geohash
	choosedAddress: null,//
	addressIndex: null,//
	needValidation: null,//
	cartId: null, //id
	sig: null,//sig
	orderParam: null,//
	orderMessage: null, //
	orderDetail: null, //
	login: true,//
	imgPath:null,//
	removeAddress:[],//
	addAddress:'',		//
	question: null,//
	cartPrice: null, //
}
export default new Vuex.Store({
	state,
	getters,
	actions,
	mutations,
})



